<template>        
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3>請您稍後，系統正在比對醫療資料庫...</h3><br>
        <div class="card-body">
            <div class="d-flex justify-content-center">
                <div class="spinner-border text-secondary" style="width: 3rem; height: 3rem;" role="status">
                    <span class="sr-only"></span>
                </div>
            </div>
            <br><br>
            <h5>比對您未告知之事項與理賠事由之發生是否具有因果關係</h5>   
        </div>
    </div>
</template>

<script>
export default {
    data (){
      var query = this.stringOfResult();
      return {
        time: 0,
        input: null,
        notInformedReason: this.$route.params.notInformedReason,
        query
      }
    },
    methods: {
        countDown (){
            let _this = this;
            _this.time--;
        },
        stringOfResult (){
            var backend = "";
            if(typeof(this.$route.params.name) !== 'undefined')
                backend += ",保險契約名稱為" + this.$route.params.name;
            if(typeof(this.$route.params.type) !== 'undefined')
                backend += ",保險種類為" + this.$route.params.type;
            if(typeof(this.$route.params.samePeople) !== 'undefined'){
                if(this.$route.params.samePeople == 1){
                    backend += ",要保人與被保險人是同一人";
                }
                else{
                    backend += ",要保人與被保險人不為同一人";
                }
            }
            if(typeof(this.$route.params.insurerCareer) !== 'undefined')
                backend += ",要保人職業為" + this.$route.params.insurerCareer;
            if(typeof(this.$route.params.insuredCareer) !== 'undefined' && this.$route.params.samePeople == 0)
                backend += ",被保險人職業為" + this.$route.params.insuredCareer;
            /*if(typeof(this.$route.params.insurerDate) !== 'undefined')
                backend += ",要保日期為" + this.$route.params.insurerDate;
            if(typeof(this.$route.params.serviceDate) !== 'undefined'){
                if(this.$route.params.serviceDate === ""){
                     backend += ",承保日期為不知道";
                }
                else{
                    backend += ",承保日期為" + this.$route.params.serviceDate;
                }
            }*/
            if(typeof(this.$route.params.disease) !== 'undefined')
                backend += ",實際罹患疾病為" + this.$route.params.disease;
            if(typeof(this.$route.params.sufferTiming) !== 'undefined')
                backend += ",您" + this.$route.params.sufferTiming + "知悉您罹患此疾病為";
            if(typeof(this.$route.params.notInformedReason) !== 'undefined')
                backend += ",當初為何會發生填寫與實際情形不符的情形為" + this.$route.params.notInformedReason;
            if(typeof(this.$route.params.settlementReason) !== 'undefined')
                backend += ",理賠事由為何為" + this.$route.params.settlementReason;
            // if(typeof(this.$route.params.settlementDate) !== 'undefined')
            //     backend += ",理賠事由發生日期為" + this.$route.params.settlementDate;
            if(typeof(this.$route.params.insurerKnow) !== 'undefined')
                backend += ",保險公司是否已知您有未據實告知之情事為" + this.$route.params.insurerKnow;
            return backend.substr(1, backend.length);
        }
    },
    mounted() {
        let _this = this;
        _this.time = 3;
        setInterval(_this.countDown, 1000);
    },
    watch: {
        time: function(newVal){
            if(newVal == 0){
                if(this.notInformedReason == '有告知業務員'){
                    this.$router.push({
                        name: 'result1-1',
                        params: {
                            query: this.query
                        }
                    });
                }
                else if(this.notInformedReason != '有告知業務員'){
                    this.$router.push({
                        name: 'result2-1',
                        params: {
                            query: this.query
                        }
                    });
                }
                else{
                    this.$router.push({
                        name: 'result4',
                        params: {
                            query: this.query
                        }
                    });
                }
            }
        }
    }
};
</script>

